___CSS_LOADER_EXPORT___.push([module.id, `/* Chat.css */
.chat-container.enhanced {
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  border-radius: 10px;
  overflow: hidden;
  background-color: #fff;
  font-family: 'Arial', sans-serif;
}

.chat-title {
  background-color: #1ba494;
  color: white;
  padding: 15px;
  text-align: center;
  margin: 0;
}

.chat-box {
  height: 400px;
  overflow-y: auto;
  padding: 15px;
  background-color: #f9f9f9;
}

.chat-bubble {
  margin-bottom: 10px;
  padding: 10px 15px;
  border-radius: 20px;
  max-width: 70%;
  word-wrap: break-word;
}

.chat-bubble.sent {
  background-color: #dcf8c6;
  margin-left: auto;
  text-align: right;
}

.chat-bubble.received {
  background-color: #fff;
  border: 1px solid #ddd;
  text-align: left;
}

.sender {
  font-size: 0.8em;
  color: #555;
  display: block;
  margin-bottom: 5px;
}

.shared-image {
  max-width: 100%;
  border-radius: 10px;
  margin-top: 5px;
}

.chat-input-container {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #fafafa;
}

.chat-input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 20px;
  margin-right: 10px;
}

.image-upload-input {
  margin-right: 10px;
}

.send-button {
  padding: 10px 20px;
  background-color: #1ba494;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.leaderboard-page {
  /* Centering the content */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  /* Padding/margins for breathing room */
  margin: 0 auto;
  padding: 20px;

  /* Font settings */
  font-family: "Roboto", sans-serif; 
  color: #00796b; 
}

.leaderboard-page h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.leaderboard-page p {
  font-size: 1.2em;
  line-height: 1.5;
}
